import axios from "axios"
import { USER_LOGIN_FAIL, USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_LOGOUT, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS } from "../constants/userConstants"

export const login =(email, err, password) => async (dispatch) =>{
    try{
        dispatch({
            type:USER_LOGIN_REQUEST
        })
        const config ={
            headers:{
                'Content-Type':'application/json'
            }
        }
        const {data} = await axios.post('/api/users/login',{email,password},config)

        dispatch({
            type:USER_LOGIN_SUCCESS,
            payload: data
        })
        localStorage.setItem('userInfo', JSON.stringify(data))

    }
    catch(error){
        dispatch({
            type:USER_LOGIN_FAIL,
            payload: err.response && err.response.data.message ? err.response.data.message:err.message,
        })

    }
}

export const logout =() => async (dispatch) =>{

    localStorage.removeItem('userInfo');
    dispatch({type: USER_LOGOUT});
}

export const register =(email, name, err, password) => async (dispatch) =>{
    try{
        dispatch({
            type:USER_REGISTER_REQUEST
        })
        const config ={
            headers:{
                'Content-Type':'application/json'
            }
        }
        const {data} = await axios.post('/api/users',{name, email,password},config)

        dispatch({
            type:USER_REGISTER_SUCCESS,
            payload: data
        })
        dispatch({
            type:USER_LOGIN_SUCCESS,
            payload: data
        })

        localStorage.setItem('userInfo', JSON.stringify(data))

    }
    catch(error){
        dispatch({
            type:USER_LOGIN_FAIL,
            payload: err.response && err.response.data.message ? err.response.data.message:err.message,
        })

    }
}